// contact section

.contact {
    padding-top: 0;

    &_header {
        margin-bottom: 30px;

        &-text {
            margin: 15px 0 30px;
        }

        &-wrapper {
            .title {
                font-weight: 600;
                font-size: 20px;
            }

            .link {
                color: $theme1;

                &:hover {
                    color: $primary;
                }
            }
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .contact {
        &_header {

            &-text {
                max-width: 480px;
            }
        }
    }
}

@media screen and (min-width: 767.98px) {
    .contact {
        &_header {
            margin-bottom: 40px;
        }
    }
}

@media screen and (min-width: 991.98px) {
    .contact {
        &_header {
            margin-bottom: 50px;

            &-text {
                margin: 25px 0 30px;
            }

            &-wrapper {
                .title {
                    margin-bottom: 10px;
                }
            }
        }
    }
}

@media screen and (min-width: 1199.98px) {
    .contact {
        &_header {
            margin-bottom: 0;
            max-width: 480px;
        }
        &_form {
            width: 100%;
            max-width: calc(100% - 480px - 60px);
        }
    }
}