// map section

.map {
    padding-top: 0;

    &_header {
        text-align: center;
        max-width: 530px;
        margin: 0 auto 30px;
    }

    &_container {
        height: 400px;
    }
}

// media queries

@media screen and (min-width: 767.98px) {
    .map {
        &_header {
            margin-bottom: 40px;
        }

        &_container {
            height: 550px;
        }
    }
}