// feedback form component

.contact_form {
    @include Card;
    padding: 30px;

    &-row {
        margin-bottom: 20px;

        &:last-of-type {
            margin-bottom: 0;
        }

        .column {
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;

            &:last-of-type {
                margin-bottom: 0;
            }

            #feedbackMessage {
                min-height: 145px;
                padding: 10px 20px;
            }
        }
    }

    .btn {
        width: 100%;
        margin-top: 30px;
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .contact_form {
        padding: 70px 40px;

        &-row {
            display: flex;
            justify-content: space-between;
            margin-bottom: 20px;

            .column {
                width: calc(50% - 10px);
                margin-bottom: 0;
            }

            &:last-of-type {
                .column {
                    width: 100%;
                }
            }
        }

        .btn {
            width: fit-content;
            margin-left: auto;
            margin-top: 40px;
        }
    }
}